import Camera from '@/engine/Camera.js'

export default {
	state: () => ({
		camera: null,
	}),
	mutations: {
		setCamera(state, payload) {
			state.camera = Camera.createCamera(payload);
		}
	},
	actions: {
		initCameraAction({commit, getters, dispatch}) {
			commit('setCamera', getters.resolution);
			dispatch('initCameraControlsAction');
		}
	},
	getters: {
		camera (store) {
			return store.camera
		},
	}
}