import * as Cell from '@/engine/HexCell.js';

export default {
	state: () => ({
		mapSize: {},
		cells: [],
		cellByGroupIdMap: new Map(),
		selectedCell: null,
	}),
	mutations: {
		addCell(state, payload) {
			const x = payload.x;
			const y = payload.y; 
			if (!state.cells[x]) state.cells[x] = [];
			state.cells[x][y] = payload;
			state.cellByGroupIdMap.set(payload.getGroupId(), payload);
		},
		setMapSize(state, payload) {
			state.mapSize = {
				width:  +payload.width,
				height: +payload.height
			}

		},
		setSelectedCell(state, payload) {
			state.selectedCell = payload;
		},
		clearState(state){
			state.cells = [];
			state.mapSize = {};
			state.cellByGroupIdMap = new Map();
			state.selectedCell = null;
		}
	},
	actions: {
		buildMapAction({commit, getters, dispatch}) {
			const outer = Cell.outer; // получаем радиусы шестиугольника 
			const inner = Cell.inner; 

			const {width, height} = getters.getFromMapFile('mapSize');
			const cells = getters.getFromMapFile('cells');

			for (let k = 0; k < cells.length; k++) {

				const z = cells[k].x;
				const i = cells[k].y;
				const matName = cells[k].type;
				const mat = getters.getModelByName(matName);
				const theta = cells[k].theta;
				const obj = Cell.createCell(mat, theta);
				dispatch('addToSceneAction', obj.getGroup());

				obj.x = z;
				obj.y = i;
				obj.theta = theta;
				obj.type = matName;
				const xx = (i+z*0.5 - Math.round(z/2))*2*inner;
				const yy = 0;
				const zz =  z*1.5*outer;
				obj.position = {
					x: xx,
					y: yy,
					z: zz
				}
				obj.getGroup().position.set(xx,yy,zz);
				commit('addCell', obj);
			}

			commit('setMapSize', {width,height});
			dispatch('readyRendererAction')
		},
		buildMapFromJSONAction({commit, getters, dispatch}, json) {
			const outer = Cell.outer; // получаем радиусы шестиугольника 
			const inner = Cell.inner; 

			console.log(json);

			const {width, height} = json.mapSize;
			const cells = json.cells


			for (let k = 0; k < cells.length; k++) {

				const z = cells[k].x;
				const i = cells[k].y;
				const matName = cells[k].type;
				const mat = getters.getModelByName(matName);
				const theta = cells[k].theta;
				const obj = Cell.createCell(mat, theta);
				dispatch('addToSceneAction', obj.getGroup());

				obj.x = z;
				obj.y = i;
				obj.theta = theta;
				obj.type = matName;
				commit('addCell', obj);
				obj.getGroup().position.set((i+z*0.5 - Math.round(z/2))*2*inner, 0, z*1.5*outer)

			}
			commit('setMapSize', {width,height});
			dispatch('readyRendererAction')
		},
		destroyMapAction({commit}){
			return new Promise(resolve => {
				commit('clearState');
				resolve();
			})
		},
		rotateSelectedCellAction({getters}, payload) {
			getters.selectedCell.rotate(getters.selectedCell,payload)
		},
		changeTypeSelectedCellAction ({getters}, payload) {
			getters.selectedCell.changeModel(getters.getModelByName(payload));
			getters.selectedCell.type = payload;
		}
	},
	getters: {
		cellsToJSON(store){
			return store.cells.flatMap(x=>x)
		},
		mapSize(store) {
			return store.mapSize
		},
		cells(store) {
			return store.cells;
		},
		getCellByGroupId (store) {
			return (id) => {
				return store.cellByGroupIdMap.get(id)
			}
		},
		selectedCell (store) {
			return store.selectedCell
		}
	}
}