export default {
	state: () => ({
		modalsetVisible: true,
		officeVisible: false,
		marketVisible: false,
		schoolVisible: false,
		houseVisible: false,
	}),
	mutations: {
		setModalsetVisible (state, payload) {
			state.modalsetVisible = payload
			if (!state.modalsetVisible) {
				state.officeVisible =  false;
				state.marketVisible = false;
				state.schoolVisible = false;
				state.houseVisible = false;
			}
		},
		setBuildDialogVisible(state, payload) {
			state.modalsetVisible = true;
			state[payload+'Visible'] = true 
		}
	},
	actions: {
		exitAction ({commit}, payload) {
			commit('setModalsetVisible', payload);
		},
		enableBuildDialog({commit}, payload) {
			commit('setBuildDialogVisible', payload)
		}
	},
	getters: {
		modalsetVisible (state) {
			return state.modalsetVisible
		},
		officeVisible (state) {
			return state.officeVisible
		},
		marketVisible (state) {
			return state.marketVisible
		},
		schoolVisible (state) {
			return state.schoolVisible
		},
		houseVisible (state) {
			return state.houseVisible
		},

	}
}