import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'

export default { 
	load(link, callback, progressCallback) {
		const loader = new GLTFLoader();
		loader.loadAsync(link, progressCallback)
		.then (( glb ) => {
			const object = glb.scene
			if (glb.animations)
				object.children[0].animations = glb.animations;
			object.scale.set(10,10,10)
			object.position.set(0,0.0,0)
			callback(object);
		})
	}
}