<template>
	<svg
		xmlns="http://www.w3.org/2000/svg"
		xmlns:sodipodi="http://sodipodi.sourceforge.net/DTD/sodipodi-0.dtd"
		xmlns:inkscape="http://www.inkscape.org/namespaces/inkscape"
		viewBox="0 0 100 100"
		version="1.1"
		width="250"
	>

		<path
			style="opacity:1;fill:#ff4a2a;fill-opacity:1;stroke:#000000;stroke-width:0.1;stroke-opacity:1"
			id="path817"
			d="M 
			22.35,31.5
			22.35,56.5
			44,69
			65.65,56.5
			65.65,31.5
			44,19 Z"
			:transform='rotateBack'
		/>
		<path
			style="opacity:1;fill:#ff0000;fill-opacity:1;stroke:#000000;stroke-width:0.1;stroke-opacity:1"
			id="path817"
			d="M 
			22.35,31.5
			22.35,56.5
			44,69
			65.65,56.5
			65.65,31.5
			44,19 Z"
			:transform='rotate'
		/>
		<text 
			x="35"
			y="48"
		>{{progress.toFixed()+'%'}}</text>


</svg>
</template>

<script>
export default {
	/* 
	0.35,12.5
		 0.35,37.5
		 22,50
		 43.65,37.5
		 43.65,12.5
		 22,0

	*/

	data: () => {
		return {
			angle: 45,
			angleBack: 0,
		}
	},
	computed: {
		rotate () {
			return `rotate(${this.angle} 44 44)`;
		},
		rotateBack () {
			return `rotate(${this.angleBack} 44 44)`;
		},
		progress () {
			return this.$store.getters.loaderProgress
		}
	},
	mounted () {
		setInterval(()=> {
			this.angle = (this.angle + 4) % 360;
			this.angleBack = (this.angleBack - 7) % 360;
		}, 25);
	}

}
</script>

<style scoped>
	text {
		font-size: 14px;
		fill: white;
	}
</style>