<template>
	<div class="d-flex align-items-center px-5">
		<div class="col-1 px-0 d-flex align-items-center">
			<img style="width:32px" :src="require('@/assets/person.svg')">
			<!--span class="fs-5 mx-2" style="color: white;">гость</span-->
			<!--img style="width:32px" :src="require('@/assets/settings.svg')"-->
		</div>
		<div class="d-md-none d-lg-block col-2 px-0 d-flex justify-content-center align-items-center">
			<span class="fs-5" style="color: white;">lvl: 2</span>
			<span class="ms-2 me-1 fs-5" style="color: white;">30 / 100</span>
			<img style="width:32px" :src="require('@/assets/exp.png')">
		</div>
		<div class="col-1 px-0 d-flex justify-content-center align-items-center">
			<span class="ms-2 me-1 fs-5" style="color: white;">100</span>
			<img style="width:32px" :src="require('@/assets/jcoin.png')">
		</div>
		<div class="col px-0 d-flex justify-content-center align-items-center">
			<span class="me-1 fs-5" style="color: white;">30 / 30</span>
			<img style="width:32px" :src="require('@/assets/energy.svg')">
		</div>
		<div class="col px-0 d-flex justify-content-center align-items-center">
			<span class="me-1 fs-5" style="color: white;">2</span>
			<img style="width:32px" :src="require('@/assets/time.png')">
		</div>
		<div class="col px-0 d-flex justify-content-center align-items-center">
			<span class="me-1 fs-5" style="color: white;">8 / 8</span>
			<img style="width:32px" :src="require('@/assets/move.svg')">
			<div class="ms-1 text-white fs-5">{{coords}}</div>
		</div>
		<div class="col px-0 d-flex justify-content-end align-items-center">
			<img style="height:32px;" :src="require('@/assets/exit.svg')">
		</div>
		
	</div>
</template>

<script>
export default {
	computed: {
		coords () {
			if (!this.$store.getters.hero) return 0;
			const X = this.$store.getters.hero.coord.x;
			const Y = this.$store.getters.hero.coord.y;
			//const Z = this.$store.getters.hero.coord.side;

			return `X: ${X.toFixed(0)} Y: ${Y.toFixed(0)}`
		},
		moves () {
			return 0;
		},
		camera () {
			if (!this.$store.getters.camera) return 0
			const pos = this.$store.getters.camera.position;
			const rot = this.$store.getters.camera.rotation;
			return JSON.stringify({
				x: pos.x.toFixed(2),
				y: pos.y.toFixed(2),
				z: pos.z.toFixed(2),
				xx: (rot.x/Math.PI * 180).toFixed(2),
				yy: (rot.y/Math.PI * 180).toFixed(2),
				zz: (rot.z/Math.PI * 180).toFixed(2),
			});
		}
	}
}
</script>

