const ROTATE_LEFT = -1;
const ROTATE_RIGHT = 1;

const THETA_0_DEGREES = 0;
const THETA_60_DEGREES = Math.PI/3;
const THETA_120_DEGREES = 2*Math.PI/3;
const THETA_180_DEGREES = Math.PI;
const THETA_240_DEGREES = 4*Math.PI/3;
const THETA_300_DEGREES = 5*Math.PI/3;

const THETA_CONST = {
	THETA_0_DEGREES,
	THETA_60_DEGREES,
	THETA_120_DEGREES,
	THETA_180_DEGREES,
	THETA_240_DEGREES,
	THETA_300_DEGREES,
};

const THETA_ARRAY = [
	THETA_0_DEGREES,
	THETA_60_DEGREES,
	THETA_120_DEGREES,
	THETA_180_DEGREES,
	THETA_240_DEGREES,
	THETA_300_DEGREES
]

function getThetaByRotating (current, dir) {
	let index = 0;
	for (let i = 0; i < THETA_ARRAY.length; i++) {
		if (Math.abs(THETA_ARRAY[i] - current) < 0.001) index = i;
	}
	if ((index + dir) > 5 ) index = 0;
	else if ((index + dir) < 0) index = 5;
	else index = index + dir;
	return THETA_ARRAY[index];
}


function getRandomTheta () {
	return THETA_ARRAY[Math.round(Math.random()*5)]
}

export {
	ROTATE_LEFT,
	ROTATE_RIGHT,
	THETA_CONST,
	THETA_ARRAY,
	getThetaByRotating,
	getRandomTheta
}