import {WebGLRenderer, Vector2, Raycaster} from 'three';

export default {
	createRenderer(domElement) {
		const renderer = new WebGLRenderer({ antialias: true });
		renderer.setSize( domElement.clientWidth, domElement.clientHeight );
		domElement.appendChild( renderer.domElement );

		return {
			getRenderer: () => {
				return renderer
			},
			getDomElenent: () => {
				return renderer.domElement
			},
			render: (scene, camera) => {
				renderer.render(scene, camera);
			},
			setRaycastListener: (getters, callback) => {
				const linkToMeshes = getters.cells.flat().map(cell => cell.getMesh())
				renderer.domElement.addEventListener('click', (event) => {
					const pointer = new Vector2();
					pointer.x = ( event.clientX / getters.resolution.width ) * 2 - 1;
					pointer.y = - ( (event.clientY-64) / getters.resolution.height ) * 2 + 1;
					const raycaster = new Raycaster();
					raycaster.setFromCamera( pointer, getters.camera );
					const intersects = raycaster.intersectObjects( linkToMeshes, false );
					console.log(intersects)
					if (intersects.length > 0) 
						callback(getters.getCellByGroupId(intersects[0].object.parent.uuid))
					else callback();
				})
			}
		}
	}
}