<template>
	<div class="row h-100 justify-content-bottom">
		<div class="col-12 h-75">
			<textarea
				ref="textarea"
				style="overflow-y: auto;"
				class="form-control h-100 text-warning bg-dark fs-4"
				id="exampleFormControlTextarea1"
				readonly
				v-model="log"
				rows="4"
			></textarea>
		</div>
		<div class="col-12 d-flex pa-0">
			<input 
				class="form-control text-warning bg-dark fs-4"
				type="text"
				placeholder="Type your command..."
				aria-label="Type command"
				v-model="command"
				@keyup.enter="handle"
				autofocus 
			>
			<button 
				class="btn btn-warning"
				type="button"
				:disabled="moveLock"
				@click="handle"
			>Enter</button>
		</div>
	</div>
</template>

<script>

export default {
	data: () => {
		return {
			command: '',
			log: '',
		}
	},

	computed: {
		moveLock () {
			return false;
		}
	},

	methods: {
		handle () {
			if (this.moveLock) return;
			switch (this.command.trim()) {
				case 'left()': this.handleLeft(); break;
				case 'right()': this.handleRight(); break;
				case 'goto w': this.handleGoto('w'); break;
				case 'goto q': this.handleGoto('q'); break;
				case 'goto e': this.handleGoto('e'); break;
				case 'goto a': this.handleGoto('a'); break;
				case 'goto d': this.handleGoto('d'); break;
				case 'goto s': this.handleGoto('s'); break;
				case 'down()': this.handleDown(); break;
				case 'rotate left': this.handleRotate(1); break;
				case 'rotate right': this.handleRotate(-1); break;
				default: this.handleError(this.command) 
			}
			this.command = '';
		},

		handleLeft() {
			this.$store.dispatch('moveLeft');
			this.addTextToLog('left()');
		},
		handleRight () {
			this.$store.dispatch('moveRight');
			this.addTextToLog('right()');
		},
		handleDown () {
			this.$store.dispatch('moveDown');
			this.addTextToLog('down()');
		},
		handleGoto (d) {
			//this.$store.dispatch('moveUp');
			//this.addTextToLog('up()');
			switch(d) {
				
				case 'w': 
					this.$store.dispatch('checkHeroMoveAction')
					.then(result => {
						if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
					})
					

					break;
				case 'q': 
					this.$store.dispatch('heroRotateAction', 1)
						.then(() => {
							this.$store.dispatch('checkHeroMoveAction')
							.then(result => {
								if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
							})
						}); break;
						
				case 'e': 
					this.$store.dispatch('heroRotateAction', -1)
						.then(() => {
							this.$store.dispatch('checkHeroMoveAction')
							.then(result => {
								if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
							})
						}); break;
				case 'a': 
					this.$store.dispatch('heroRotateAction', 2)
						.then(() => {
							this.$store.dispatch('checkHeroMoveAction')
							.then(result => {
								if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
							})
						}); break;
				case 'd': 
					this.$store.dispatch('heroRotateAction', -2)
						.then(() => {
							this.$store.dispatch('checkHeroMoveAction')
							.then(result => {
								if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
							})
						}); break;
				case 's': 
					this.$store.dispatch('heroRotateAction', 3)
						.then(() => {
							this.$store.dispatch('checkHeroMoveAction')
							.then(result => {
								if (result == -1) this.addTextToLog('Движение невозможно: впереди припятствие!');
							})
						}); break;
			}

			
			
			this.addTextToLog('goto ' + d);
			
		},
		handleRotate (dir) {
			//this.$store.dispatch('moveUp');
			//this.addTextToLog('up()');
			this.$store.dispatch('heroRotateAction', dir)

			this.addTextToLog(`rotate ${dir==1?'left':'right'}`);

		},
		handleError (command) {
			this.addTextToLog('Команда '+ command + ' не существует')
		},

		addTextToLog(text) {
			this.log += '\t > ' + text + '\n';
			const textarea = this.$refs.textarea;
			if(textarea.selectionStart == textarea.selectionEnd) {
				textarea.scrollTop = textarea.scrollHeight;
			}
		},
	}
}
</script>

