import loader from '@/engine/ModelsLoader.js'
import * as MD from '@/engine/ModelsData.js'

export default {
	state: () => ({
		models:{},
		loaded: 0,
		hero: null,
		visible: true,
		progress: 0,
	}),
	mutations: {
		setModel(state, {name, value}) {
			state.models[name] = value;
			state.loaded++;
			state.progress = state.loaded/13*100
		},
		setHero(state, payload) {
			state.hero = payload
			state.loaded++;
			state.progress = 100
		},
		setVisible(state, payload) {
			state.visible = payload;
		},
	},
	actions: {
		loadMaterials({commit, dispatch, state}) {
			return new Promise (resolve => {
				for (let key in MD.MODEL_PATHS) {
					loader.load(
						MD.MODEL_PATHS[key], // ссылка
						(object) => { // обработчик
							// добавляем модель
							commit('setModel', {name:key, value:object});
							// если все модели уже загружены
							if (state.loaded == 12) {
								// загружаем модель героя
								loader.load(MD.HERO_TYPE_PATH, (object) => {

									object.scale.set(2,2,2)
									object.position.set(51.96,0, 60)
									object.coord = {
										x:4,
										y:3,
										side: 1,
									};
									object.rotation.set(0,Math.PI/6,0)

									commit('setHero', object)
									commit('setVisible', false)
									dispatch('initRendererAction')

									resolve()
								})
							}
					});
				}
			})
		},
	},
	getters: {
		getModelByName (store) {
			return (name) => {
				return store.models[name]
			}
		},
		hero(store) {
			return store.hero
		},
		loaderVisible(state) {
			return state.visible
		},
		loaderProgress(state) {
			return state.progress
		},
	}
}