import {Color, ShaderMaterial, DoubleSide, CircleGeometry, Mesh, RingGeometry, MeshBasicMaterial, Group} from 'three';
import {getThetaByRotating} from './AngleUtils.js'

const outer = 10.0;
const inner = outer * Math.sqrt(3)/2;

function createCell (model, theta) {

		var uniforms = {
			"color1" : {
				type : "c",
				value : new Color(0x805b3e)
			},
			"color2" : {
				type : "c",
				value : new Color(0x7c593d)
			},
		};
		
		var fShader = `
			#include <common>

			uniform vec3 iResolution;
			uniform float iTime;
			uniform vec3 color1;
			uniform vec3 color2;
			varying vec2 vUv;
			float random (vec2 st) {
				return fract(sin(dot(st.xy, vec2(12.9898,78.233))) * 43758.5453123);
			}

			void mainImage(out vec4 fragColor, in vec2 fragCoord) {
				
				//vec3 cl = mix(color2, color1, sqrt((vUv.y*vUv.y)+(vUv.x*vUv.x))/75.0);
				vec3 cl = mix(color2, color1, vUv.x);
				vec3 res = vec3(cl.x+sin(random(vUv))*0.2, cl.y+sin(random(vUv))*0.2, cl.z+cos(random(vUv))*0.2);
				fragColor = vec4(res, 1.0);
			}
			void main() {
				mainImage(gl_FragColor, gl_FragCoord.xy);
			}
		`;
		var vShader = `
			varying vec2 vUv;
			void main() {
				vUv = uv;
				gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1.0);
			}
		`;

		// Create material
		var hexMaterial = new ShaderMaterial({
			uniforms: uniforms,
			fragmentShader: fShader,
			vertexShader: vShader,
			side: DoubleSide
		});
		const hexGeometry = new CircleGeometry( 10, 6, Math.PI/6);
		const mesh = new Mesh(hexGeometry, hexMaterial);
		mesh.rotation.set(Math.PI/2,0,0);

		const ringGeometry = new RingGeometry( 10, 9.9, 6, 1, Math.PI/6);
		const ringMaterial = new MeshBasicMaterial( { color: 0xe69100, side: DoubleSide, opacity:0.25, transparent:true } );
		const ringMesh = new Mesh( ringGeometry, ringMaterial );
		ringMesh.rotation.set(Math.PI/2,0,0);
		ringMesh.position.set(0,0.1,0)
		ringMesh.renderOrder = 200

		const group = new Group();
		group.add(mesh);
		group.add(ringMesh);

		let m = model.clone();
		group.add(m);
		m.rotation.set(0,theta,0)


		return {
			getGroup: () => {
				return group
			},
			getGroupId: () => {
				return group.uuid
			},
			getMesh: () => {
				return mesh
			},
			select: () => {
				ringMesh.material.color.setHex( 0xff0000 );
				ringMesh.material.transparent = false
			},
			unselect: () => {
				ringMesh.material.color.setHex( 0xe69100 );
				ringMesh.material.transparent = true
			},
			rotate: (cell, delta) => {
				console.log(getThetaByRotating(m.rotation.y, delta))
				m.rotation.set(0, getThetaByRotating(m.rotation.y, delta),0)
				//if (m.rotation.y + delta > 2*Math.PI) m.rotation.set(0, m.rotation.y + delta - 2*Math.PI,0)
				//else if (m.rotation.y + delta < 0) m.rotation.set(0, m.rotation.y + delta + 2*Math.PI, 0)
				//else 
				cell.theta = m.rotation.y;
			},
			changeModel: (model) => {
				const nm = model.clone();
				nm.rotation.set(0,m.rotation.y,0);
				group.remove(m);
				group.add(nm);
				m = nm;
			}
		}
}

export {
	outer,
	inner,
	createCell
}