import * as CT from '@/engine/ModelsData.js'


export default {
	state: () => ({
		
	}),
	mutations: {
		
	},
	actions: {
		checkHeroMoveAction ({getters, dispatch}) {
			// todo check очки

			const diffs = (getters.hero.coord.x%2 != 0) ?
			[
				{x:0,z:1}, // side == 0
				{x:1,z:0}, // side == 1
				{x:1,z:-1}, // side == 2
				{x:0,z:-1}, // side == 3
				{x:-1,z:-1}, // side == 4
				{x:-1,z:0}, // side == 5
			] : [
				{x:0,z:1}, // side == 0
				{x:1,z:1}, // side == 1
				{x:1,z:0}, // side == 2
				{x:0,z:-1}, // side == 3
				{x:-1,z:0}, // side == 4
				{x:-1,z:1}, // side == 5
			]

			const diff = diffs[getters.hero.coord.side];
			const tc = getters.cells[getters.hero.coord.x+diff.x][getters.hero.coord.y+diff.z];
			const tc_type = tc.type;

			console.log(tc_type)

			if (tc_type == CT.CELL_TYPE_MOUNTAIN 
				|| tc_type == CT.CELL_TYPE_TREES 
				|| tc_type == CT.CELL_TYPE_MIDSTONE
				|| tc_type == CT.CELL_TYPE_BUSHES) {
				console.log("HHH")
				return -1;
			}
			// офис
			if (tc_type == CT.CELL_TYPE_OFFICE) {
				dispatch('enableBuildDialog', 'office')
				return 1;
			}
			// дом
			if (tc_type == CT.CELL_TYPE_HOUSE) {
				dispatch('enableBuildDialog', 'house')
				return 1;
			}
			// школа
			if (tc_type == CT.CELL_TYPE_SCHOOL) {
				dispatch('enableBuildDialog', 'school')
				return 1;
			}
			// рынок
			if (tc_type == CT.CELL_TYPE_MARKET) {
				dispatch('enableBuildDialog', 'market')
				return 1;
			}
			console.log("KKKHHH")
			dispatch('heroMoveAction');

		},

	},
	getters: {
		
	}
}