export default {
	state: () => ({
		cameraControls: null,
	}),
	mutations: {
		setCameraControls (state, payload) {
			state.cameraControls = payload;
		}
	},
	actions: {
		initCameraControlsAction({/*commit, getters,*/ dispatch}) {
			//commit('setCameraControls', 
				//Camera.createControls( getters.camera, getters.renderer.getDomElenent()));
			dispatch('initSceneAction');
		}
	},
	getters: {
		cameraControls (store) {
			return store.cameraControls
		},
	}
}