import Renderer from '@/engine/Renderer.js'
import {LoopOnce, AnimationMixer, Clock, VectorKeyframeTrack, AnimationClip, /*, AnimationClip*/} from 'three';

export default {
	state: () => ({
		canvas: null,
		resolution: {},
		renderer: null,
		updatables: [],
		updatablesP: [],
		idleAction: null,
	}),
	mutations: {
		initRenderer(state) {
			state.resolution.height = state.canvas.clientHeight;
			state.resolution.width = state.canvas.clientWidth;
			state.renderer = Renderer.createRenderer(state.canvas);
		},
		setCanvas(state, payload) {
			state.canvas = payload;
		},
		setIdleAction (state, payload) {
			state.idleAction = payload;
		}
	},
	actions: {
		setCanvasAction ({commit}, payload) {
			commit("setCanvas", payload)
		},
		initRendererAction( {commit, dispatch}) {
			commit('initRenderer')
			dispatch('initCameraAction');
		},
		readyRendererAction ({getters, commit}) {

			window.addEventListener( 'resize', () => {
				console.log("hereeeee")
				const canvas = getters.canvas;
				getters.resolution.height = canvas.clientHeight;
				getters.resolution.width = canvas.clientWidth;
				getters.camera.aspect = canvas.clientWidth / canvas.clientHeight;
				getters.camera.updateProjectionMatrix();
				getters.renderer.getRenderer().setSize( canvas.clientWidth, canvas.clientHeight );

			});

			getters.renderer.setRaycastListener(getters, (intersects) => {
				if (intersects) {
					if ( getters.selectedCell != intersects ) { 
						if ( getters.selectedCell ) getters.selectedCell.unselect();
						intersects.select();
						commit('setSelectedCell', intersects);
					}
				} else {
					if ( getters.selectedCell ) getters.selectedCell.unselect();
					commit('setSelectedCell', null);
				}
			})
			
			let animate = () => {
				getters.updatables.forEach(call => call());
				getters.updatablesP.forEach(call => call());
				requestAnimationFrame( animate );
				getters.renderer.render( getters.scene, getters.camera );
			}

			animate();

			getters.camera.position.set(45, 15, 47)
			//getters.cameraControls.target.set(getters.hero.position.x,getters.hero.position.y, getters.hero.position.z )
			getters.camera.rotation.set(-148/180*Math.PI, -24/180*Math.PI, -165/180*Math.PI)
			//getters.cameraControls.update();
			console.log(getters.camera)
			const heroMixer = new AnimationMixer( getters.hero.children[0] );
			const stayAction = heroMixer.clipAction(getters.hero.children[0].animations[1]);

			stayAction.play();
			console.log(stayAction)
			const clock4 = new Clock();
			getters.updatablesP.push(() => {
				const delta = clock4.getDelta();
				heroMixer.update( delta );
			})
			commit('setIdleAction', stayAction);

		},

		heroMoveAction({getters}){
			// позиции
			const hp = getters.hero.position;

			const diffs = (getters.hero.coord.x%2 != 0) ?
			[
				{x:0,z:1}, // side == 0
				{x:1,z:0}, // side == 1
				{x:1,z:-1}, // side == 2
				{x:0,z:-1}, // side == 3
				{x:-1,z:-1}, // side == 4
				{x:-1,z:0}, // side == 5
			] : [
				{x:0,z:1}, // side == 0
				{x:1,z:1}, // side == 1
				{x:1,z:0}, // side == 2
				{x:0,z:-1}, // side == 3
				{x:-1,z:0}, // side == 4
				{x:-1,z:1}, // side == 5
			]

			const diff = diffs[getters.hero.coord.side];

			console.log("before", diff, getters.hero.coord);

			const tc = getters.cells[getters.hero.coord.x+diff.x][getters.hero.coord.y+diff.z].position;

			const positionKF = new VectorKeyframeTrack('.position',
			[0, 5],
			[hp.x,hp.y,hp.z,tc.x, tc.y, tc.z],);

			// создаем анимацию для перремещения сцены
			const moveBlinkClip = new AnimationClip('move-n-blink', 5, [positionKF]);
			const mixer = new AnimationMixer(getters.hero);
			const action = mixer.clipAction(moveBlinkClip);
			action.loop = LoopOnce;
			action.clampWhenFinished = true;
			action.play();
			const clock = new Clock();
			getters.updatables.push(() => {
				const delta = clock.getDelta();
				mixer.update( delta );
			});

			// создаем анимацию для анимации героя
			const clock2 = new Clock();
			const clips = [getters.hero.children[0].animations[0]];
			const mixer1 = new AnimationMixer( getters.hero.children[0] );
			clips.forEach( function ( clip ) {
				mixer1.clipAction( clip ).play();
			} );

			getters.updatables.push(()=>{
				const delta = clock2.getDelta();
				mixer1.update( delta );
			})

			// создаем анимацию для камеры
			const cp = getters.camera.position
			const positionCKF = new VectorKeyframeTrack('.position',
			[0, 5],
			[cp.x, cp.y, cp.z,cp.x+(tc.x-hp.x),cp.y,cp.z+(tc.z-hp.z)]);
			const moveBlinkClipCamera = new AnimationClip('move-camera', 5, [positionCKF]);
			const mixerCamera = new AnimationMixer(getters.camera);
			const actionCamera = mixerCamera.clipAction(moveBlinkClipCamera);
			actionCamera.loop = LoopOnce;
			actionCamera.clampWhenFinished = true;
			actionCamera.play();
			const clock3 = new Clock();
			getters.updatables.push(() => {
				const delta = clock3.getDelta();
				mixerCamera.update( delta );
			});


			//mixer.addEventListener( 'finished', onLoopFinished );
			// проверка на отключение анимации
			mixer.addEventListener( 'finished', () => {
			//getters.updatables.push(() => {

				if (!action.isRunning()) {

					getters.updatables.splice(0);
					// здесь должна быть операция приведения героя в исходное положение
					// и вызов события "движение окончено"
					getters.hero.coord.x += diff.x;
					getters.hero.coord.y += diff.z;
					console.log("after", getters.hero.coord);
					getters.idleAction.setEffectiveWeight(1);
					
				}
			})

			getters.idleAction.setEffectiveWeight(0);
		},

		heroRotateAction({getters}, dir) {
			return new Promise( (resolve) => {
			const rotation = getters.hero.rotation;
			const positionKF = new VectorKeyframeTrack('.rotation[y]',
			[0, 2],
			[rotation.y, rotation.y+dir*Math.PI/3]);

			getters.hero.coord.side -= dir;
			if (getters.hero.coord.side == 6 ) getters.hero.coord.side = 0;
			else if (getters.hero.coord.side == 7 ) getters.hero.coord.side = 1;
			else if (getters.hero.coord.side == 8 ) getters.hero.coord.side = 2;
			else if (getters.hero.coord.side == -1 ) getters.hero.coord.side = 5;
			else if (getters.hero.coord.side == -2 ) getters.hero.coord.side = 4;
			else if (getters.hero.coord.side == -3 ) getters.hero.coord.side = 3;

			// создаем анимацию для перремещения сцены
			const moveBlinkClip = new AnimationClip('rotation-hero', 2, [positionKF]);
			const mixer = new AnimationMixer(getters.hero);
			const action = mixer.clipAction(moveBlinkClip);
			action.loop = LoopOnce;
			action.clampWhenFinished = true;
			action.play();
			const clock = new Clock();
			getters.updatables.push(() => {
				const delta = clock.getDelta();
				mixer.update( delta );
			});

			// создаем анимацию движени камеры
			const hp = getters.hero.position; // O
			const cp = getters.camera.position; // A
			const xx = (cp.x-hp.x)*Math.cos(dir*Math.PI/3) + (cp.z-hp.z)*Math.sin(dir*Math.PI/3)+hp.x;
			const zz = -(cp.x-hp.x)*Math.sin(dir*Math.PI/3) + (cp.z-hp.z)*Math.cos(dir*Math.PI/3)+hp.z;
			const positionCKF = new VectorKeyframeTrack('.position',
			[0, 2],
			[cp.x, cp.y, cp.z,xx, cp.y, zz]);
			const moveCameraClip = new AnimationClip('move-camera', 2, [positionCKF]);
			const mixerCamera = new AnimationMixer(getters.camera);
			const action2 = mixerCamera.clipAction(moveCameraClip);
			action2.loop = LoopOnce;
			action2.clampWhenFinished = true;
			action2.play();
			const clock2 = new Clock();
			getters.updatables.push(() => {
				const delta = clock2.getDelta();
				mixerCamera.update( delta );
				getters.camera.lookAt(hp.x, 7, hp.z);
			});

			if (!action.isRunning()) {
					getters.updatables.splice(0);
				}

			getters.updatables.push(() => {
				if (!action.isRunning() && !action2.isRunning()) {
					getters.updatables.splice(0);
					getters.camera.lookAt(hp.x, 7, hp.z);
					resolve();
				}
			});
			})
		}

	},
	getters: {
		resolution (store) {
			return store.resolution
		},
		renderer (store) {
			return store.renderer
		},
		canvas (store) {
			return store.canvas
		},
		updatables (store) {
			return store.updatables
		},
		updatablesP (store) {
			return store.updatablesP
		},
		idleAction (store) {
			return store.idleAction
		}
	}
}