import Scene from '@/engine/Scene.js'
import Light from '@/engine/Light.js'

export default {
	state: () => ({
		scene: null
	}),
	mutations: {
		setScene(state) {
			state.scene = Scene.createScene();
		},
		addToScene(state, payload) {
			state.scene.add(payload)
		},
		clearScene(state) {
			state.scene.clear()
		}
	},
	actions: {
		initSceneAction({commit, dispatch, getters}) {
			commit('setScene');
			//commit('addToScene', Light.createDirectionalLight());
			commit('addToScene', Light.createPointGridLight());
			commit('addToScene', getters.hero)
			dispatch('buildMapAction')
		},
		addToSceneAction({commit}, payload) {
			commit('addToScene', payload);
		},
		addToSceneLightAction({commit}) {
			commit('addToScene', Light.createPointGridLight());
		},
		addHeroToSceneAction({commit, getters}) {
			commit('addToScene', getters.hero)
			
		},
		clearSceneAction({commit}) {
			commit('clearScene');
		}
	},
	getters: {
		scene (store) {
			return store.scene
		},
	}
}